




































import { Component, Vue } from "vue-property-decorator";
import Experience from "@/views/Profile/experience.vue";
import Publication from "@/views/Profile/publication.vue";
import Interest from "@/views/Profile/interest.vue";
import { researchInterests, publications, experiences } from "@/data/profile";

@Component({
  components: {
    Experience,
    Publication,
    Interest
  }
})
export default class Profile extends Vue {
  interests: object[] = researchInterests;
  publications: object[] = publications;
  experiences: object[] = experiences;
}
