export const maininfo = {
  head: "ARCollabVis",
  headintro: "Augmented Reality + Visualization",
  title1: "Introduction",
  title1_1: "About",
  content1_1:
    "This is registered as a Provincial Innovation Project 2017. Worked with Yangfan Liang and Runhao Bao. Directed by Prof. Yingcai Wu from CAD&CG State Key lab in Zhejiang University. ",
  title1_2: "Background",
  content1_2:
    "For any entity commerce that need to rent or buy places of interest for running business, site selection plays as an key part towards success. A good site saves capital, attracts potential targets, convinent for regular customers to reach and is also less possible to suffer from unexpected emergences. Site selection really disturbs business runners who want a good start. Thus, a site-selecting tool for not only experts but also people who have little knowledge about this is of greet need.",
  tltle1_3: "Motivation",
  content1_3:
    "Firstly, urban data we use to visualize can be multi-dimensional. To visualize data in a 2D way can indeed provide sufficient information to pick up a site, but presenting it in 3D way is also a good solution for users to immersively dive into the data itself and get a much more clear picture of the visualization. So, presenting data in AR technology is the most motivation of our work.<br><br>Secondly, The development of mobile and AR technology makes it possible to combine both. Since headset equipment is still expensive for most citizens, mobile phones have their advantage on popularity. Therefore, mobile AR is a new and cost-effective way to experience AR technology.<br><br>Meanwhile, since mobile phones are universal among us, presenting a collaboration between a mobile AR platform and a web service framework or database service can be feasible in analyzing. It helps to multi-users to make decisions.",
  title2: "What are you trying to solve",
  content2:
    "We explore the possibility of an AR-based Collaborative platform that allows users to select business locations. Say that your group needs to select a location to run business, we need to collaboratively make a decision based on the massive data which presented in the platform. The problem can be: How to display information such as traffic, weather, unexpected disasters clearly in a VR-3D world. How to automatically calculate several candidates meeting users' demands. We present the prototype, trying to offer a solution to urban-context based decision making problem, in which we describe how multiple users can walk through the workflow of the decision-making process by collaboration in an immersive environment.",
  title3: "Design Process",
  content3: "This platform comprises 3 main phases:"
};

