export const askquestion1 = [
  {
    q: "Why this mode?",
    ans:
      "We did a little survey among a few fellow students, people tend to prefer an overview of the whole picture for better decision. They feel it more natural to switch from a 2D map to a 3D overview map before dive/immerse into the model."
  },
  {
    q: "What can we do in this mode?",
    ans:
      "Users can take a walk to see the whole area, and return back to this mode whenever they want in the future modes."
  },
  {
    q: "Why using this design?",
    ans:
      "This is because users tend to review the whole picture during or after the process of comparing and exploring. They lose the sight of the whole picture when comparing the candidates one by one. So they need to review the whole."
  }
];

export const askquestion2 = [
  {
    q: "Why this mode?",
    ans:
      "Since the candidate number are widely shortened during the 2D process, users should dive in and compare candidates one by one using the embedded data."
  },
  {
    q: "What can we do in this mode?",
    ans:
      "We can choose two candidates to compare and collaboratively let other users know which buildings/candidates you are looking at. Together with a Message Panel to show you and other users’ activity record. Besides, you can also compare the data separately or together by click the button “Compare”."
  },
  {
    q: "Why using this design?",
    ans:
      "Collaboration is always the theme of this project. Users should know other’s work. They should also be able to look at the data separately or together, so that they can get a better understanding of what they stands for."
  }
];

export const askquestion3 = [
  {
    q: "Why this mode?",
    ans:
      "This is the deepest level a user can dive into. Users can truly wander in the room to see the buildings, to see how they were placed or sheltered."
  },
  {
    q: "What can we do in this mode?",
    ans:
      "They can walk around to see the buildings. Also, the collaboration in the last mode can also been accessed."
  },
  {
    q: "Why using this design?",
    ans:
      "Because the whole process(Overview--Compare—Explore) is a scaling process for users to dive into. They can be more and more immersive analyzing the system and the situation. This is kind of a new contribution because other Collaborative AR systems are not with so many layers divided by depth."
  }
];
